import React, { Component } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import Home from "../pages/home/HomeComponent";
import Splash from "../pages/splash/Splash";
import Education from "../pages/education/EducationComponent";
import Experience from "../pages/experience/Experience";
import Opensource from "../pages/opensource/Opensource";
import Contact from "../pages/contact/ContactComponent";
import Projects from "../pages/projects/Projects";
import Resume from "../pages/resume/Resume";
import { settings } from "../portfolio.js";
// import Error404 from "../pages/errors/error404/Error";

export default class Main extends Component {
    render () {
        if (settings.isSplash) {
            return (
                <div>
                    <Router basename="/">
                        <Routes>
                            <Route
                                path="/"
                                exact
                                element={
                                    <Splash {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/home"
                                element={ <Home {...this.props} theme={this.props.theme} />}
                            />
                            <Route
                                path="/experience"
                                exact
                                element={
                                    <Experience {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/education"
                                element={
                                    <Education {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/opensource"
                                element={
                                    <Opensource {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/contact"
                                element={
                                    <Contact {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/splash"
                                element={
                                    <Splash {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/projects"
                                element={
                                    <Projects {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/resume"
                                element={
                                    <Resume {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                element={
                                    <Home {...this.props} theme={this.props.theme} />
                                }
                            />
                        </Routes>
                    </Router>
                </div>
            );
        } else {
            return (
                <div>
                    <Router basename="/">
                        <Routes>
                            <Route
                                path="/"
                                exact
                                element={ <Home {...this.props} theme={this.props.theme} />}
                            />
                            <Route
                                path="/home"
                                element={ <Home {...this.props} theme={this.props.theme} />}
                            />
                            <Route
                                path="/experience"
                                exact
                                element={
                                    <Experience {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/education"
                                element={
                                    <Education {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/opensource"
                                element={
                                    <Opensource {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/contact"
                                element={
                                    <Contact {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/projects"
                                element={
                                    <Projects {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                path="/resume"
                                element={
                                    <Resume {...this.props} theme={this.props.theme} />
                                }
                            />
                            <Route
                                element={
                                    <Home {...this.props} theme={this.props.theme} />
                                }
                            />
                        </Routes>
                    </Router>
                </div>
            );
        }
    }
}